.price {
	min-height: 100vh;
	margin: 2rem 0;
}
.price__wrapper {
	display: flex;
	align-items: flex-end;
	gap: 1rem;
	border-radius: 0.75rem;
	box-shadow: 0rem 0rem 2.19rem 0rem rgba(0, 0, 0, 0.13);
	background: rgb(255, 255, 255);
	width: 100%;
	max-width: 73.13rem;
	padding: 1rem 1.5rem;
}
.price__content {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}
.price__level {
	display: flex;
	flex-direction: column;
	gap: 1rem;
}
.price__indicate {
	display: flex;
	flex-flow: row wrap;
	align-items: center;
	justify-content: center;
	margin-bottom: 10px;
	padding: 2rem 0.5rem 0 0;
	gap: 5px;
}
.price__indicator {
	max-width: 5.38rem;
	width: 100%;
	height: 0.81rem;
	border-radius: 5.56rem;
	border: 0.06rem solid rgb(201, 201, 201);
}
.price__indicator.active {
	background: linear-gradient(
			-7.89deg,
			rgba(0, 0, 0, 0.2) 20.906%,
			rgba(0, 0, 0, 0) 51.583%
		),
		rgb(69, 196, 54);
}
.price__content h1 {
	text-align: center;
	color: rgb(34, 34, 34);
	font-size: clamp(1rem, 3.5vw, 2.13rem);
	font-weight: 600;
	line-height: 2.56rem;
	text-align: center;
}
.price__level-content {
	display: flex;
	flex-flow: row wrap;
	align-items: center;
	justify-content: space-between;
}
.price__level-content-lvl3 {
	justify-content: center;
}
.lvl3-img {
	max-width: 15rem;
}
.price__level-content-grid {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(13.81rem, 1fr));
	grid-auto-rows: auto;
	place-items: center;
	gap: 26px;
}
.price__level-content-grid img {
	width: 100%;
	object-fit: cover;
}
.price__level-content-grid .item__lvl4 {
	padding: 1rem 1.5rem;
	width: 100%;
	height: 100%;
}
.price__level-content div {
	display: flex;
	align-items: center;
	justify-content: center;
}
.price__level-item {
	width: fit-content;
	
	border: 0.06rem solid rgba(0, 0, 0, 0.09);
	border-radius: 0.56rem;
	background: rgb(255, 255, 255);
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;
}
.lvl2-wrapper {
	display: flex;
	justify-content: center;
	gap: 1rem;
}
.price__level-item .price__img-lvl4 {
	max-width: 7rem;
	height: 100%;
	object-fit: cover;
}
.price__level-item img {
	max-width: 8.06rem;
	width: 100%;
	height: 7.41rem;
	object-fit: cover;
}
.price__level-item .price__level-imgFrame {
	max-width: 4.1rem;
}
.price__level-content .price__level-item.active {
	border: 1px solid #45c436;
}
.price__level-titleHolder {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	gap: 1rem;
	margin-bottom: 47px;
}
.price__title {
	color: rgb(34, 34, 34);
	font-size: clamp(1rem, 2.5vw, 2.13rem);
	font-weight: 600;
	line-height: 2.56rem;
	letter-spacing: 0%;
	text-align: left;
}
.counter {
	color: rgb(34, 34, 34);
	font-size: clamp(0.7, 1.5vw, 0.88rem);
	font-weight: 400;
	line-height: 1.06rem;
	text-align: center;
}
.price__level-desc {
	color: rgb(34, 34, 34);
	font-size: clamp(1rem, 1.5vw, 1.13rem);
	font-weight: 400;
	line-height: 1.31rem;
	text-align: center;
}
.price__level-btn {
	margin: 1rem 0;
	color: rgb(255, 255, 255);
	font-size: clamp(1rem, 1.5vw, 1.13rem);
	font-weight: 400;
	line-height: 1.31rem;
	letter-spacing: 0%;
	text-align: center;
	padding: 26px 76px;
	border-radius: 7.88rem;
	background: linear-gradient(
			-16.76deg,
			rgba(0, 0, 0, 0.2) -12.863%,
			rgba(0, 0, 0, 0) 66.504%
		),
		rgb(69, 196, 54);
	display: flex;
	flex-direction: column;
	align-self: center;
}
.price__level-btn:hover {
	cursor: pointer;
}
/* SECTION 2 */
.price__level-decor {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;
	gap: 0.5rem;
	position: relative;
}
.price__level-decor img {
	width: 100%;
	object-fit: cover;
}
.decor--3 {
	max-width: 37.27rem;
	width: 100%;
	object-fit: cover;
}
.price__bg {
	background: linear-gradient(to right, #ffd25a 50%, #45c436 50%);
	padding: 16.5rem 0 0 0;
	overflow: hidden;
	border-radius: 0.56rem;
	position: relative;
}
.price__level-decor-sun {
	position: absolute;
	top: 0.5rem;
	right: 0.5rem;
	width: 15%;
	object-fit: cover;
}
.price__level-decor-dots {
	position: absolute;
	top: -10rem;
	left: 5rem;
	max-width: 4rem;
	object-fit: cover;
}
.price__level-decor-profile {
	position: absolute;
	height: 30rem;
	width: 100%;
	object-fit: cover;
	bottom: 2rem;
	left: 50%;
	transform: translateX(-50%);
	z-index: 100;
}
.price__level-info {
	max-width: 20rem;
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	border-radius: 0.56rem;
	box-shadow: 0rem 0rem 0.94rem 0rem rgba(0, 0, 0, 0.09);
	background: linear-gradient(
			161.77deg,
			rgba(255, 255, 255, 0) 19.849%,
			rgb(255, 255, 255) 82.432%
		),
		rgba(255, 255, 255, 0.86);
	z-index: 200;
	padding: 0.5rem 1.5rem;
}
.prcie__level-info--flexer {
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: space-between;
}
.prcie__level-info--flexer--4 {
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	gap: 23px;
}
.price__level-info img {
	max-width: 10.37rem;
	width: 100%;
	object-fit: cover;
}
.price__level-info h5 {
	color: rgb(34, 34, 34);
	font-size: clamp(1rem, 1.5vw, 1.63rem);
	font-weight: 600;
	text-align: left;
}
.price__level-info button {
	color: rgb(69, 196, 54);
	border: 0.06rem solid rgb(69, 196, 54);
	background: #fff;
	border-radius: 0.56rem;
	padding: 12px 33px;
	font-size: clamp(1rem, 1.5vw, 1.13rem);
	font-weight: 400;
	line-height: 1.31rem;
	letter-spacing: 0%;
	transition: 0.3s all ease-in-out;
	margin: 0;
}
.price__level-info button:hover {
	color: #fff;
	background: rgb(69, 196, 54);
	cursor: pointer;
}
.price__level-info p {
	color: rgb(34, 34, 34);
	font-size: clamp(0.9rem, 2.5vw, 1rem);
	font-weight: 300;
	line-height: 1.19rem;
	letter-spacing: 0%;
	text-align: left;
}
.bonus .container {
	display: flex;
	align-items: center;
	justify-content: space-between;
}
.bonus__wrapper {
	display: flex;
	align-items: center;
	justify-content: space-between;
	gap: 1.5rem;
	border-radius: 0.75rem;
	box-shadow: 0rem 0rem 2.19rem 0rem rgba(0, 0, 0, 0.13);
	background: rgb(255, 255, 255);
	padding: 2rem 1.5rem;
}
.gift {
	width: 100%;
	object-fit: cover;
	object-position: center;
	margin-bottom: 5rem;
	margin-left: -2rem;
}
.bonus__controlls {
	display: flex;
	flex-flow: row wrap;
	align-items: center;
	justify-content: center;
	gap: 0.5rem;
}
.bonus__content label {
	display: flex;
	align-items: center;
	gap: 1rem;
	position: relative;
}
.bonus__content label input {
	box-sizing: border-box;
	border: 0.06rem solid rgb(231, 231, 231);
	border-radius: 3.81rem;
	background: rgb(242, 242, 242);
	padding: 20px 153px 20px 53px;
	color: #000;
	font-size: clamp(1rem, 2.5vw, 1.13rem);
	font-weight: 400;
	line-height: 1.31rem;
	text-align: left;
	width: 100%;
}
.bonus__content label input::placeholder {
	color: rgb(197, 197, 197);
}
.bonus__content label img {
	position: absolute;
	left: 1.5rem;
	top: 50%;
	transform: translateY(-50%);
}
