@import './font.css';
@import './media.css';

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}
input,
button,
div {
	box-sizing: border-box;
	border: none;
	background: none;
	outline: none;
}
::before,
::after {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
:is(ul, li, a) {
	list-style: none;
	text-decoration: none;
	color: #000000;
}
html {
	scroll-behavior: smooth;
}
#form {
	scroll-margin: 150px;
}
body {
	font-weight: 400;
	font-size: 16px;
	background: #ffffff;
	font-family: 'SFPRODISPLAY';
}
.App {
	width: 100%;
	display: flex;
	flex-direction: column;
	overflow: hidden;
}
.main {
	flex-grow: 1;
}
.main .section__title {
	width: fit-content;
	color: rgb(34, 34, 34);
	font-size: clamp(1rem, 10vw, 2.88rem);
	font-weight: 600;
	margin: 148px auto;
	text-align: center;
	position: relative;
}
.price .section__title::before {
	content: '';
	position: absolute;
	left: -5%;
	top: 15px;
	box-shadow: 0 0 30px 10px rgb(82, 243, 65);
	background: rgb(69, 196, 54);
	max-width: 1.75rem;
	width: 100%;
	height: 1.75rem;
	border-radius: 50%;
}
.form .section__title {
	max-width: 38rem;
	text-align: left;
	margin: 168px 0 32px 0;
}
.faq .section__title {
	max-width: 38rem;
	text-align: left;
	margin: 168px 0 32px 0;
}
.section__title .green {
	color: #45c436;
	font-size: clamp(1rem, 10vw, 2.88rem);
	font-weight: 600;
}
.section__title span {
	color: rgb(34, 34, 34);
	font-size: clamp(0.9rem, 1.5vw, 1.63rem);
	font-weight: 400;
	line-height: 1.94rem;
	letter-spacing: 0%;
	text-align: left;
}
.container {
	max-width: 1041px;
	width: 100%;
	margin: 0 auto;
	padding: 0 15px;
}
* {
	color: #000;
}
